.App {
  text-align: center;
  scroll-behavior: auto;
}

#App-message {
  font-size: 70px;
  color: white;
  background-color: #343a40;
}

.nav-item {
  margin-right: 10px;
}

.App-logo {
  height: 45px;
  pointer-events: none;
  margin-left: 10px;
}

h1 {
  font-size: 50px;
  margin-top: 2%;
  margin-bottom: 2%;
}

.Content-bio {
  font-size: 30px;
}

.Content-portfolio {
  text-align: center;
}

.Content-portfolio li, p {
  margin-left: 20%;
  margin-right: 20%;
  font-size: 30px;
  padding-top: 1%;
}

.Content-portfolio h2 {
  font-size: 40px;
  padding-top: 1%;
}

.Content-portfolio img {
  width: 40%;
  margin-top: 1%;
  margin-bottom: 1%;
  margin-left: 20%;
  margin-right: 20%;
}

.image-row {
  margin-top: 1%;
  margin-left: 20%;
  margin-right: 20%;
}

.image-column3 {
  float: left;
  width: 32%;
  padding: 0.5%;
}

.image-column3 img{
  width: 100%;
  margin-left: 0;
  margin-right: 0;
}

.image-column2 {
  float: left;
  width: 49%;
  padding: 0.5%;
}

.image-column2 img{
  width: 100%;
  margin-left: 0;
  margin-right: 0;
}

.image-row::after {
  content: "";
  clear: both;
  display: table;
}

.Content-copyright {
  margin-top: 2%;
  margin-bottom: 2%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
